* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
.slider{
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.video-slider{
  width: 720px;
  height: 480px;
  object-fit: cover;
  pointer-events: none;
}
@media screen and (min-width: 768px) and (max-width: 992px){
  .video-slider{
    width: 600px;
    height: 400px;
    object-fit: cover;
  }
  .right-arrow{
    font-size: 2rem !important;
  }
  
  .left-arrow{
    font-size: 2rem !important;
  }
}
@media screen and (min-width: 576px) and (max-width: 767px){
  .video-slider{
    width: 480px;
    height: 320px;
    object-fit: cover;
  }
  .right-arrow{
    font-size: 1.5rem !important;
  }
  
  .left-arrow{
    font-size: 1.5rem !important;
  }
}
@media screen and (min-width: 340px) and (max-width: 575px){
  .video-slider{
    width: 300px;
    height: 200px;
    object-fit: cover;
  }
  .right-arrow{
    font-size: 1.1rem !important;
  }
  
  .left-arrow{
    font-size: 1.1rem !important;
  }
}
@media screen and (max-width: 339px){
  .video-slider{
    width: 240px;
    height: 160px;
    object-fit: cover;
  }
  .right-arrow{
    font-size: 1rem !important;
  }
  
  .left-arrow{
    font-size: 1rem !important;
  }
}

.right-arrow{
  font-size: 3rem;
  color: #27D184;
  z-index: 1;
  cursor: pointer;
  user-select: none;
}

.left-arrow{
  font-size: 3rem;
  color:#27D184;
  z-index: 1;
  cursor: pointer;
  user-select: none;
}

.slide, .slide-active {
  display: flex;
  justify-content: center;
}

.mail-link{
    text-align: center;
    margin-top: 24px;
    color: #fff;
    text-decoration: none;
    font-size: 1rem;
    cursor: pointer;
}
.mail-link:hover{
  transition: all 0.2s ease-in-out;
  color: #27D184;
}